.intro_banner {
  background-image: url("https://www.riotgames.com/darkroom/800/0dc32f4fda89caf9c9af983fbb731c97:7a98f43cc1a5193569198818c1620290/arcane-1.jpeg");
}

@media (min-width: 756px) {
  .intro_banner {
    background-image: url("https://www.riotgames.com/darkroom/2000/abbeb99c6eb67f7d1a20985de52ee214:fc42a46fc8797a9d3040236017a4051e/arcane-1.jpeg");
  }
}

.intro_banner {
  width: calc(100vw - 0);
  height: calc(100vh - 70px);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.video_intro {
  background: #252423;
}

.video_intro_text {
  color: #fff;
  font-weight: bold;
  left: 50%;
  text-align: center;
  font-size: 3rem;
  top: 50%;
  transform: translate(-50%, -50%);
}
.news-intro {
  height: 105vh;
}
.news-intro-container::before {
  z-index: -1;
  display: block;
  position: absolute;
  left: 0;
  content: " ";
  width: 100%;
  height: 105vh;
  background-image: url("https://www.riotgames.com/darkroom/1000/413f5cdaa5272fcbb94d62a44233861c:26c29b6def3e8e41f480e308e612ced6/ryan-crosby.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(1.2) brightness(0.7);
  clear: both;
}

@media screen and (max-width: 768px) {
  .lastestNews_container {
    height: 100%;
    margin-right: 0 !important;
    margin-top: 2rem;
    flex: 0 0 100% !important;
  }

  .news_items {
    flex: 0 0 100% !important;
  }

  #whathappening_title {
    font-size: 2rem !important;
  }

  #seemore_btn {
    position: absolute;
    bottom: 2%;
    left: 50%;
    transform: translateX(-50%);
  }
}
